.buy {
  background-color: $color-ghost-white;

  position: relative;
  padding: 121px 0;

  @include vp-1199 {
    padding: 101px 0 99px;
  }

  @include vp-767 {
    padding: 65px 0 67px;
  }
}

.buy__title {
  font-size: 54px;
  line-height: 64px;
  letter-spacing: 1px;

  text-align: center;
  margin-top: 0;
  margin-bottom: 16px;

  @include vp-1199 {
    font-size: 42px;
    line-height: 50px;

    margin-bottom: 19px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 38px;
  }
}

.buy__controls {
  position: relative;
  width: 343px;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 38px;
  margin-left: auto;

  border-bottom: 2px solid $color-crete;

  @include vp-1199 {
    width: 332px;
    margin-bottom: 45px;
  }

  @include vp-767 {
    width: 290px;
    margin-bottom: 34px;
  }
}

.buy__container {
  display: flex;
  justify-content: center;
}

.buy__button {
  font-family: $ff-roboto-condensed;
  font-size: 20px;
  line-height: 30px;
  font-weight: 400;
  color: transparentize($color-default-dark, 0.5);
  background-color: $color-transparent;
  border: none;
  cursor: pointer;

  padding-right: 0;
  padding-bottom: 11px;
  padding-left: 0;
  margin-bottom: -1.5px;

  @include hover-focus {
    &:not(:disabled) {
      color: $color-default-dark;
      border-bottom: 2px solid $color-torch-red;
      outline: none;
    }
  }

  &.is-active {
    &:not(:disabled) {
      color: $color-default-dark;
      border-bottom: 2px solid $color-torch-red;
      cursor: default;
    }
  }

  &:disabled {
    border-bottom: 2px solid $color-ghost-white;
    cursor: default;
  }

  &:not(:last-child) {
    margin-right: 45px;

    @include vp-1199 {
      margin-right: 52px;
    }

    @include vp-767 {
      margin-right: 32px;
    }
  }

  @include vp-1199 {
    font-size: 18px;
    line-height: 27px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 24px;

    padding-right: 7px;
    padding-bottom: 14px;
  }
}

.buy__cards {
  width: 100%;
  display: flex;
  gap: 40px;

  @include vp-1199 {
    flex-direction: column;
  }

  @include vp-767 {
    gap: 35px;
  }
}

.buy__bg-img {
  position: absolute;
  right: 0;
  bottom: -149px;

  @include vp-1199 {
    bottom: -124px;
  }

  @include vp-767 {
    display: none;
  }
}

.buy__bg-img img {
  @include vp-1199 {
    width: 347px;
    height: 444px;
  }
}
