.main-footer {
  padding: 39px 0;

  background-color: $color-black-russian;

  @include vp-1199 {
    padding: 43px 0 46px;
  }

  @include vp-767 {
    padding: 46px 0 54px;
  }
}

.main-footer__social {
  margin-left: auto;
  margin-top: 0;

  @include vp-767 {
    margin-left: 0;
    margin-top: 34px;
  }
}

.main-footer__list {
  margin-right: 20px;

  @include vp-1199 {
    margin-right: 10px;
  }
}

.main-footer__logo {
  margin-right: auto;

  @include vp-767 {
    position: absolute;
    top: 3px;
    left: 0;
  }
}
